import { Injectable } from '@angular/core';
import { Functions } from '../../utils/functions';
import { Api } from '../api/api';

/*
  Generated class for the HospitalsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class HospitalsProvider {

  constructor(public api: Api) {
    console.log('Hello HospitalsProvider Provider');
  }

  query(params?: any) {
    return this.api.get('hospitals', params).map((data: any) => {

      let items: any[] = Functions._sortBy(data.data, 'name');

      return items;
    });
  }

}



