
export class Functions {
  static _sortBy(items: any[], property: string) {
    function compare(a,b) {
      if (a[property] < b[property])
        return -1;
      if (a[property] > b[property])
        return 1;
      return 0;
    }

    items.sort(compare);

    return items;
  }
}



