import 'rxjs/add/operator/toPromise';

import { Injectable } from '@angular/core';

import { Api } from '../api/api';
import { Storage } from '@ionic/storage';
import { __USER_LOGGED } from '../../app/app.component';
import { Events } from 'ionic-angular';

/**
 * Most apps have the concept of a User. This is a simple provider
 * with stubs for login/signup/etc.
 *
 * This User provider makes calls to our API at the `login` and `signup` endpoints.
 *
 * By default, it expects `login` and `signup` to return a JSON object of the shape:
 *
 * ```json
 * {
 *   status: 'success',
 *   user: {
 *     // User fields your app needs, like "id", "name", "email", etc.
 *   }
 * }Ø
 * ```
 *
 * If the `status` field is not `success`, then an error is detected and returned.
 */

export const __USER = '_user';

@Injectable()
export class User {
  _user: any;

  constructor(public api: Api, private storage: Storage, private events: Events) { }

  /**
   * Send a POST request to our login endpoint with the data
   * the user entered on the form.
   */
  login(accountInfo: any) {

      return new Promise((resolve, reject) => {
          let payload: any = {
              client_id: 1,
              client_secret: '8kBuAAMtIQT2HfwT5RzSbkl4xKUSMtoODszC4jXe',
              grant_type: 'password'
          };

          if (accountInfo.username && accountInfo.password) {
              payload.username = accountInfo.username;
              payload.password = accountInfo.password;
          }


        let seq = this.api.login('oauth/token', payload);

        seq.then((res: any) => {
          // If the API returned a successful response, mark the user as logged in
            this._loggedIn(res);
            this.api.get('user').subscribe((data: any) => {
              this._user = data.data;
              this.storage.set(__USER, this._user);
              this.events.publish(__USER_LOGGED, this._user);
              resolve(res);
            });

        }, err => {
          console.error('ERROR', err);
          reject(err);
        });
      });
  }

  /**
   * Send a POST request to our signup endpoint with the data
   * the user entered on the form.
   */
  signup(accountInfo: any) {
    let seq = this.api.register('register-user', accountInfo);

    seq.then((res: any) => {
      // If the API returned a successful response, mark the user as logged in
      if (res.status == 'success') {
        this._loggedIn(res);
      }
    }, err => {
      console.error('ERROR', err);
    });

    return seq;
  }

  /**
   * Log the user out, which forgets the session
   */
  logout() {
    this._user = null;
    this.api.logout();
  }

  /**
   * Process a login/signup response to store user data
   */
  _loggedIn(resp) {
    this._user = resp;
  }

  user(): any {
    return this.storage.get(__USER)
  }

  setUser(user) {
    this._user = user;
    this.storage.set(__USER, this._user);
  }

  save(item) {
    return this.api.put('users/' + item.id, item);
  }
}



