import { Injectable } from '@angular/core';
import { Functions } from '../../utils/functions';
import { Api } from '..';

/*
  Generated class for the HospitalDischargeMotiveProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class HospitalDischargeMotiveProvider {

  constructor(public api: Api) {
    console.log('Hello HospitalDischargeMotiveProvider Provider');
  }

  query(params?: any) {
    return this.api.get('hospital-discharge-motives', params).map((data: any) => {

      let items: any[] = Functions._sortBy(data.data, 'name');

      return items;
    });
  }
}



