import { Injectable } from '@angular/core';
import { DrugModel } from '../../models/drug.model';
import { Functions } from '../../utils/functions';
import { Api } from '..';

/*
  Generated class for the DrugsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class DrugsProvider {

  constructor(public api: Api) {
    console.log('Hello DrugsProvider Provider');
  }

  query(params?: any) {
    return this.api.get('drugs', params).map((data: any) => {

      let items: any[] = Functions._sortBy(data.data, 'name');

      return DrugModel.fromResponse(items);
    });
  }

}



