import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { TokenManagerProvider } from '../../providers/token-manager/token-manager';

// export const BASE_URL = 'http://mappab-api-prod.us-east-2.elasticbeanstalk.com/';
// export const BASE_URL = 'http://mappab-api-staging.us-east-2.elasticbeanstalk.com/';

export const BASE_URL = 'http://mappab.local/';
export const API_URL = BASE_URL + 'api';

/**
 * Api is a generic REST Api handler. Set your API url first.
 */
@Injectable()
export class Api {
  url: string = API_URL;

  token: string;

  constructor(public http: HttpClient, private tokenManagerProvider: TokenManagerProvider) {
  }

  login(endpoint: string, body: any, reqOpts?: any): Promise<any> {
    reqOpts = reqOpts ? reqOpts : {};
    return new Promise((resolve, reject) => {
      this.http.post(BASE_URL + endpoint, body, reqOpts).subscribe((data: any) => {
        this.tokenManagerProvider.setToken(data.access_token, data.expires_in + Date.now());
        resolve(data);
      }, (err) => {
        reject(err);
      });
    });
  }

  register(endpoint: string, body: any, reqOpts?: any): Promise<any> {
    reqOpts = reqOpts ? reqOpts : {};
    return new Promise((resolve, reject) => {
      this.http.post(API_URL + '/'+ endpoint, body, reqOpts).subscribe((data: any) => {
        // this.tokenManagerProvider.setToken(data.access_token, data.expires_in + Date.now());
        resolve(data);
      }, (err) => {
        reject(err);
      });
    });
  }

  logout() {
    this.tokenManagerProvider.destroyToken();
  }

  get(endpoint: string, params?: any, reqOpts?: any) {
    if (!reqOpts) {
      reqOpts = {
        params: new HttpParams()
      };
    }

    // Support easy query params for GET requests
    if (params) {
      reqOpts.params = new HttpParams();
      for (let k in params) {
        reqOpts.params = reqOpts.params.set(k, params[k]);
      }
    }

    reqOpts.headers = this.getHeaders();

    return this.http.get(this.url + '/' + endpoint, reqOpts);
  }

  post(endpoint: string, body: any, reqOpts?: any) {
    reqOpts = reqOpts ? reqOpts : {};
    reqOpts.headers  = this.getHeaders();
    return this.http.post(this.url + '/' + endpoint, body, reqOpts);
  }

  put(endpoint: string, body: any, reqOpts?: any) {
    reqOpts = reqOpts ? reqOpts : {};
    reqOpts.headers  = this.getHeaders();
    return this.http.put(this.url + '/' + endpoint, body, reqOpts);
  }

  delete(endpoint: string, reqOpts?: any) {
    reqOpts = reqOpts ? reqOpts : {};
    reqOpts.headers  = this.getHeaders();
    return this.http.delete(this.url + '/' + endpoint, reqOpts);
  }

  patch(endpoint: string, body: any, reqOpts?: any) {
    reqOpts = reqOpts ? reqOpts : {};
    reqOpts.headers  = this.getHeaders();
    return this.http.patch(this.url + '/' + endpoint, body, reqOpts);
  }

  getHeaders() {
    let headers = new HttpHeaders().set('Authorization', 'Bearer ' + this.getToken());
    return headers;
  }

  getToken(): string {
    return this.tokenManagerProvider.getStaticToken();
  }
}



