import { Injectable } from '@angular/core';
import { Api } from '..';

/*
  Generated class for the StatesProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class StatesProvider {

  constructor(public api: Api) {
    console.log('Hello StatesProvider Provider');
  }

  query(params?: any) {
    return this.api.get('states', params).map((data: any) => {
      return data.data;
    });
  }

  cities(stateId) {
    return this.api.get('states/' + stateId + '/cities').map((data: any) => {
      return data;
    });
  }

  save(item: any) {
    if (!item.id) {
      return this.api.post('states', item).map((data: any) => {
        return data.data;
      });
    }

    return this.api.put('states/' + item.id, item).map((data: any) => {
      return data.data;
    });
  }

  remove(item: any) {
    return this.api.delete('states/'+item.id, item).map((data: any) => {
      return data.data;
    });
  }
}



