import { GoogleAnalytics } from "@ionic-native/google-analytics";
import { inject } from '../utils/inject';

declare var gtag;
declare var window;

export const ANALYTICS_TRACK_ID = 'UA-127707998-1';

export function AnalytictsTrack(page: string) : any {
  return function (constructor : any) {
    const LIFECYCLE_HOOKS = [
      'ionViewWillEnter',
    ];

    LIFECYCLE_HOOKS.forEach(hook => {
      constructor.prototype[hook] = function () {
        if (!window.cordova) {
          gtag('config', ANALYTICS_TRACK_ID, {
            'page_title': page,
            'page_location': page,
            'page_path': page
          });
        } else {
          let goolAnalytics = inject(GoogleAnalytics);
          goolAnalytics.startTrackerWithId(ANALYTICS_TRACK_ID).then(() => {
            goolAnalytics.trackView(page);
          });
        }
      }
    });
  }
}



