import { Injectable } from '@angular/core';
import { Functions } from '../../utils/functions';
import { Api } from '..';

/*
  Generated class for the MedicalConductProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class MedicalConductProvider {

  constructor(public api: Api) {
    console.log('Hello MedicalConductProvider Provider');
  }

  query(params?: any) {
    return this.api.get('medical-conducts', params).map((data: any) => {

      let items: any[] = Functions._sortBy(data.data, 'name');

      return items;
    });
  }

}



