import { Injectable } from '@angular/core';
import { Api } from '..';
import { Functions } from '../../utils/functions';

/*
  Generated class for the ExamTypesProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ExamTypesProvider {

  constructor(public api: Api) {
    console.log('Hello ExamTypesProvider Provider');
  }

  query(params?: any) {
    return this.api.get('exam-types', params).map((data: any) => {

      let items: any[] = Functions._sortBy(data.data, 'name');

      return items;
    });
  }

  save(item: any) {
    if (!item.id) {
      return this.api.post('exam-types', item).map((data: any) => {
        return data.data;
      });
    }

    return this.api.put('exam-types/'+item.id, item).map((data: any) => {
      return data.data;
    });
  }

  remove(item: any) {
    return this.api.delete('exam-types/'+item.id, item).map((data: any) => {
      return data.data;
    });
  }
}



