import { HttpClient, HttpClientModule } from '@angular/common/http';
import { ErrorHandler, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { Camera } from '@ionic-native/camera';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { IonicStorageModule, Storage } from '@ionic/storage';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { Items } from '../mocks/providers/items';
import { Api, Settings, User } from '../providers';
import { MyApp } from './app.component';
import { AppConfig } from './app.config';
import { Professionals } from '../providers/professionals/professionals';
import { Patients } from '../providers/patients/patients';
import { EvolutionsProvider } from '../providers/evolutions/evolutions';
import { Calendar } from '@ionic-native/calendar';
import { SchedulesProvider } from '../providers/schedules/schedules';
import { TokenManagerProvider } from '../providers/token-manager/token-manager';
import { ToastProvider } from '../providers/toast/toast';
import { ClinicsProvider } from '../providers/clinics/clinics';
import { HospitalsProvider } from '../providers/hospitals/hospitals';
import { ExamTypesProvider } from '../providers/exam-types/exam-types';
import { PatientHouseholdMonitoringProvider } from '../providers/patient-household-monitoring/patient-household-monitoring';
import { MedicalConductProvider } from '../providers/medical-conduct/medical-conduct';
import { DrugsProvider } from '../providers/drugs/drugs';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { SpecializationsProvider } from '../providers/specializations/specializations';
import { HospitalMonitoringProvider } from '../providers/hospital-monitoring/hospital-monitoring';
import { HospitalConductProvider } from '../providers/hospital-conduct/hospital-conduct';
import { HospitalDischargeMotiveProvider } from '../providers/hospital-discharge-motive/hospital-discharge-motive';
import { AccommodationsProvider } from '../providers/accommodations/accommodations';
import { StatesProvider } from '../providers/states/states';
import { BrMaskerModule } from 'brmasker-ionic-3';
import { CacheModule } from "ionic-cache";
import { GoogleAnalytics } from '@ionic-native/google-analytics';
import { LoadingProvider } from '../providers/loading/loading';


// The translate loader needs to know where to load i18n files
// in Ionic's static asset pipeline.
export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

export function provideSettings(storage: Storage) {
  /**
   * The Settings provider takes a set of default settings for your app.
   *
   * You can add new settings options at any time. Once the settings are saved,
   * these values will not overwrite the saved values (this can be done manually if desired).
   */
  return new Settings(storage, {
    option1: true,
    option2: 'Ionitron J. Framework',
    option3: '3',
    option4: 'Hello'
  });
}

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    BrMaskerModule,
    CacheModule.forRoot(),
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      }
    }),
    IonicModule.forRoot(MyApp, AppConfig),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    Api,
    Items,
    User,
    TokenManagerProvider,
    Camera,
    InAppBrowser,
    SplashScreen,
    StatusBar,
    Calendar,
    Professionals,
    Patients,
    SchedulesProvider,
    { provide: Settings, useFactory: provideSettings, deps: [Storage] },
    // Keep this to enable Ionic's runtime error handling during development
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    EvolutionsProvider,
    SchedulesProvider,
    ToastProvider,
    ClinicsProvider,
    HospitalsProvider,
    ExamTypesProvider,
    PatientHouseholdMonitoringProvider,
    MedicalConductProvider,
    DrugsProvider,
    SpecializationsProvider,
    HospitalMonitoringProvider,
    MedicalConductProvider,
    HospitalConductProvider,
    HospitalDischargeMotiveProvider,
    AccommodationsProvider,
    StatesProvider,
    GoogleAnalytics,
    LoadingProvider
  ]
})
export class AppModule { }



