import { Injectable } from '@angular/core';

import { Api } from '../api/api';
import { PatientMapper } from '../../models/patient.model';
import { Functions } from '../../utils/functions';

@Injectable()
export class Patients {

  constructor(public api: Api) {
  }

  query(params?: any) {
    return this.api.get('patients', params).map((data: any) => {

      let items: any[] = Functions._sortBy(data.data, 'name');

      return items;
    });
  }

  get(id: string) {
    return this.api.get('patients/' + id).map((data: any) => {
      return data.data;
    });
  }

  add(item: PatientMapper) {
  }

  delete(item: PatientMapper) {
  }

}



