import { Injectable } from '@angular/core';
import { Functions } from '../../utils/functions';
import { Api } from '..';

/*
  Generated class for the ClinicsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ClinicsProvider {

  constructor(public api: Api) {
    console.log('Hello ClinicsProvider Provider');
  }

  query(params?: any) {
    return this.api.get('clinics', params).map((data: any) => {

      let items: any[] = Functions._sortBy(data.data, 'name');

      return items;
    });
  }

  save(item: any) {
    if (!item.id) {
      return this.api.post('clinics', item).map((data: any) => {
        return data.data;
      });
    }

    return this.api.put('clinics/'+item.id, item).map((data: any) => {
      return data.data;
    });
  }

  remove(item: any) {
    return this.api.delete('clinics/'+item.id, item).map((data: any) => {
      return data.data;
    });
  }

}



