import { Injectable } from '@angular/core';
import { Api } from '../api/api';
import { ScheduleMapper } from '../../models/schedule.model';

/*
  Generated class for the SchedulesProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class SchedulesProvider {

  constructor(public api: Api) {
    console.log('Hello SchedulesProvider Provider');
  }

  query(startsIn?: string, endsIn?: string, patientId?: string, professionalId?: string) {
    let params: string[] = [];

    if (startsIn) {
      params.push('starts_in='+startsIn);
    }
    if (endsIn) {
      params.push('ends_in='+endsIn);
    }
    if (patientId) {
      params.push('patient_id='+patientId);
    }
    if (professionalId) {
      params.push('professional_id='+professionalId);
    }

    let urlParams: string = params.length ? '?'+ params.join('&') : '';

    return this.api.get('schedules' + urlParams).map((data: any) => {
      return data.data;
    });
  }

  add(item: ScheduleMapper) {
    return this.api.post('schedules', item);
  }

  save(item: ScheduleMapper) {
    if (!item.id) {
      return this.api.post('schedules', item);
    }
    return this.api.put('schedules/' + item.id, item);
  }

  delete(item: ScheduleMapper) {
    return this.api.delete('schedules/' + item.id);
  }
}



