import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Api } from '..';

/*
  Generated class for the EvolutionsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class EvolutionsProvider {

  constructor(public http: HttpClient, private api: Api) {
    console.log('Hello EvolutionsProvider Provider');
  }

  query(params?: any) {
    return this.api.get('patient-evolutions', params).map((data: any) => {

      return data.data;
    });
  }


  patientEvolutions(patientId: string) {
    return this.api.get('patient-evolutions?patient_id='+patientId).map((data: any) => {

      let items: any[] = data.data;

      return items;
    });
  }

  save(patientEvolution: any) {
    if (!patientEvolution.id) {
      return this.api.post('patient-evolutions', patientEvolution).map((data: any) => {
        return data.data;
      });
    }

    return this.api.put('patient-evolutions/'+patientEvolution.id, patientEvolution).map((data: any) => {
      return data.data;
    });
  }

}



