import { Injectable } from '@angular/core';
import { Api } from '..';

/*
  Generated class for the PatientHouseholdMonitoringProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class PatientHouseholdMonitoringProvider {

  constructor(public api: Api) {
    console.log('Hello PatientHouseholdMonitoringProvider Provider');
  }

  query(params?: any) {
    return this.api.get('patient-monitoring', params).map((data: any) => {

      return data.datat;
    });
  }

  get(id) {
    return this.api.get('patient-monitoring/' + id).map((data: any) => {
      return data.data;
    });
  }

  save(item: any) {
    if (!item.id) {
      return this.api.post('patient-monitoring', item).map((data: any) => {
        return data.data;
      });
    }

    return this.api.put('patient-monitoring/'+item.id, item).map((data: any) => {
      return data.data;
    });
  }

}



