import { Injectable } from '@angular/core';
import { Api } from '..';
import { Functions } from '../../utils/functions';

/*
  Generated class for the HospitalMonitoringProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class HospitalMonitoringProvider {

  constructor(public api: Api) {
    console.log('Hello HospitalMonitoringProvider Provider');
  }

  patientHospitalized() {
    return this.api.get('patient-hospitalized').map((data: any) => {

      let items: any[] = Functions._sortBy(data.data, 'name');

      return items;
    });
  }
}



