import { Injectable } from '@angular/core';

import { Api } from '../api/api';
import { ProfessionalMapper } from '../../models/professional.model';

@Injectable()
export class Professionals {

  constructor(public api: Api) { }

  query(params?: any) {
    return this.api.get('professionals', params).map((data: any) => {
      return data.data;
    });
  }

  get(id: string) {
    return this.api.get('professionals/' + id).map((data: any) => {
      return data.data;
    });
  }

  add(item: ProfessionalMapper) {
  }

  delete(item: ProfessionalMapper) {
  }

}



