import { Component, ViewChild } from '@angular/core';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { TranslateService } from '@ngx-translate/core';
import { Config, Events, Nav, Platform } from 'ionic-angular';

import { FirstRunPage, MainPage, WelcomePage } from '../pages';
import { Settings } from '../providers';
import { Storage } from '@ionic/storage';
import { TokenManagerProvider } from '../providers/token-manager/token-manager';
import { UserMapper } from '../models/user.model';
import { GoogleAnalytics } from '@ionic-native/google-analytics';
import { ANALYTICS_TRACK_ID } from '../decorators/analyticts-track.decorators';

export const __TUTORIAL_READ = '__tutorial_read';

export const __USER_LOGGED = 'user:logged';

declare var document;

@Component({
  template: `
  <ion-split-pane when="md">
    <!--<ion-menu [content]="content" *ngIf="loggedUser">-->
      <!--<ion-header>-->
        <!--<ion-toolbar>-->
          <!--<ion-title>Menu</ion-title>-->
        <!--</ion-toolbar>-->
      <!--</ion-header>-->
  <!---->
      <!--<ion-content>-->
        <!--<ion-list>-->
          <!--<button menuClose ion-item *ngFor="let p of pages" (click)="openPage(p)">-->
            <!--{{p.title}}-->
          <!--</button>-->
          <!--<button menuClose ion-item (click)="logout()"> Sair </button>-->
        <!--</ion-list>-->
      <!--</ion-content>-->
  <!---->
    <!--</ion-menu>-->
    <ion-nav #content main [root]="rootPage" swipeBackEnabled="false"></ion-nav>
  </ion-split-pane>
  `
})
export class MyApp {
  rootPage: any;

  loggedUser: UserMapper;

  @ViewChild(Nav) nav: Nav;

  pages: any[] = [
    { title: 'Meus dados', component: 'ProfilePage' },
    { title: 'Agendamentos', component: 'SchedulesPage' },
    { title: 'Evoluções', component: 'EvolutionsPage' },
    { title: 'Profissionais', component: 'ProfessionalsPage' },
    { title: 'Pacientes', component: 'PatientsPage' },
    { title: 'Clínicas', component: 'ClinicsPage' },
    // { title: 'Especializações', component: 'SpecializationsPage' },
    // { title: 'Exames', component: 'ExamsPage' },
    // { title: 'Welcome', component: 'WelcomePage' },
    // { title: 'Tabs', component: 'TabsPage' },
    // { title: 'Cards', component: 'CardsPage' },
    // { title: 'Content', component: 'ContentPage' },
    // { title: 'Login', component: 'LoginPage' },
    // { title: 'Signup', component: 'SignupPage' },
    // { title: 'Master Detail', component: 'ListMasterPage' },
    // { title: 'Menu', component: 'MenuPage' },
    // { title: 'Settings', component: 'SettingsPage' },
    // { title: 'Search', component: 'SearchPage' }
  ]

  constructor(private translate: TranslateService, private platform: Platform, settings: Settings, private storage: Storage,
              private events: Events, private tokenManagerProvider: TokenManagerProvider, private config: Config,
              private goolAnalytics: GoogleAnalytics,
              private statusBar: StatusBar, private splashScreen: SplashScreen) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.initialPage();
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
    this.initTranslate();
    this.initEvents();
    this.initGoogleAnalytics();
  }

  logout() {
    this.loggedUser = null;
    this.nav.setRoot('WelcomePage');
  }

  initEvents() {
    this.events.subscribe(__USER_LOGGED, (user) => {
      // user logged. event published on login page
      this.loggedUser = user;
    });
  }

  async initialPage() {
    this.rootPage = await this.chooseInitialPage();
  }

  chooseInitialPage(): Promise<any> {
    return new Promise((resolve, reject) => {
      this.tokenManagerProvider.getToken().then(() => {
        resolve(MainPage);
        return;
      }, () => {
        this.storage.get(__TUTORIAL_READ).then((tutorialRead: boolean) => {
          if (tutorialRead) {
            resolve(WelcomePage);
            return;
          }

          resolve(FirstRunPage);
          return;
        });
      });
    });
  }


  initTranslate() {
    // Set the default language for translation strings, and the current language.
    this.translate.setDefaultLang('pt');
    const browserLang = this.translate.getBrowserLang();

    if (browserLang) {
      if (browserLang === 'zh') {
        const browserCultureLang = this.translate.getBrowserCultureLang();

        if (browserCultureLang.match(/-CN|CHS|Hans/i)) {
          this.translate.use('zh-cmn-Hans');
        } else if (browserCultureLang.match(/-TW|CHT|Hant/i)) {
          this.translate.use('zh-cmn-Hant');
        }
      } else {
        this.translate.use(this.translate.getBrowserLang());
      }
    } else {
      this.translate.use('en'); // Set your language here
    }

    this.translate.get(['BACK_BUTTON_TEXT']).subscribe(values => {
      this.config.set('ios', 'backButtonText', values.BACK_BUTTON_TEXT);
    });
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    // this.nav.setRoot(page.component);
    this.nav.push(page.component);
  }

  private initGoogleAnalytics() {
    if (!this.platform.is('cordova')) {

      let script = document.createElement("script");
      script.type = "text/javascript";
      script.src = "https://www.googletagmanager.com/gtag/js?id=" + ANALYTICS_TRACK_ID;
      document.getElementsByTagName("head")[0].append(script);

      let scriptGtag = document.createElement("script");
      scriptGtag.innerHTML = "if (!window.cordova) { window.dataLayer = window.dataLayer || []; " +
        " function gtag(){dataLayer.push(arguments);} gtag('js', new Date());}";
      document.getElementsByTagName("head")[0].append(scriptGtag);
      return;
    }
    this.goolAnalytics.startTrackerWithId(ANALYTICS_TRACK_ID)
      .then(() => {
        console.info('Google analytics is ready now');
      });
  }


}



