import { Injectable } from '@angular/core';
import { Api } from '..';
import { CacheService } from 'ionic-cache';
import { map } from 'rxjs/operators';

/*
  Generated class for the SpecializationsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class SpecializationsProvider {

  private cacheKey: string = 'specializations';

  constructor(public api: Api, private cache: CacheService) {
    console.log('Hello SpecializationsProvider Provider');
  }

  query(params?: any) {
    let request = this.api.get('specializations', params);
    return this.cache.loadFromObservable(this.cacheKey, request).pipe(map(res => res.data));
  }

  save(item: any) {
    if (!item.id) {
      return this.api.post('specializations', item).map((data: any) => {
        return data.data;
      });
    }

    return this.api.put('specializations/'+item.id, item).map((data: any) => {
      return data.data;
    });
  }

  remove(item: any) {
    return this.api.delete('specializations/'+item.id, item).map((data: any) => {
      return data.data;
    });
  }
}



